nav {
  position: fixed;
  top: 0;
  width: 100vw;
  padding: 3px 0;
  z-index: 9999;
  background-color: transparent;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style: none;
  gap: 5px;
  padding: 0;
  padding-top: 20px;
  margin: 0;
}

nav ul li {
  color: #fff;
  padding: 5px;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: 0.5s;
  box-shadow: #a8a8a856 0px 1px 0px 1px;
  text-align: center;
  width: 90px;
  height: 43px;
  border-top-left-radius: 50% 90%;
  border-bottom-right-radius: 50% 100%;
}

.nav-label {
  display: block;
  font-size: 0.6rem;
  text-align: center;
  margin-top: 5px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

nav ul li:hover {
  color: #bc0000;
  background-color: #f2ff00;
}

.active {
  background-color: #bc0000;
  zoom: 1.1;
  transform: matrix(1.1, 0, 0, 1.1, 0, 0);
}

@media (max-width: 768px) {
  nav ul li {
    font-size: 0.9rem;
    padding: 3px;
    width: 70px;
    height: 33px;
  }

  .nav-label {
    font-size: 0.5rem;
  }
}